html[data-theme="dark"] {
  background-color: #1b1b1b; }
  html[data-theme="dark"] main span, html[data-theme="dark"] main p, html[data-theme="dark"] main h4 {
    color: #fefefe; }

html[data-theme="light"] {
  background-color: #fefefe; }
  html[data-theme="light"] main span, html[data-theme="light"] main p, html[data-theme="light"] main h4 {
    color: #1b1b1b; }

html.loaded {
  transition: background-color .4s ease-in-out, color .4s ease-in-out; }

body {
  font-size: 11pt;
  margin: 0;
  overflow: hidden; }
  body, body * {
    font-family: "Noto Sans CJK KR", "Noto Sans KR", sans-serif; }

::selection {
  background-color: #ffc206;
  color: white; }

*:focus {
  outline: none; }
