.container {
  display: block;
  margin: calc(1vh + 5% - 20px) auto calc(2vh - 20px);
  width: max-content; }

main {
  margin: auto;
  position: relative;
  display: table-cell;
  min-width: 240px;
  padding: 20px; }
  main h1 {
    color: #ffc206;
    margin: 0 0 10px 0;
    font-weight: 700;
    font-size: 1.4rem;
    display: inline-block; }
  main .pivot {
    padding-right: 4px; }

.menuContainer {
  display: table;
  padding: 10px; }
  .menuContainer > div {
    display: table-cell; }
  .menuContainer > div:not(:last-of-type) {
    padding-right: 20px; }
  .menuContainer h4 {
    margin: 0 0 10px 0; }
  .menuContainer .menuList {
    min-width: 100px;
    line-height: 1.8;
    display: table; }
    @media screen and (orientation: portrait) {
      .menuContainer .menuList {
        display: flex;
        flex-direction: column; }
        .menuContainer .menuList > div[data-list]:not(:last-of-type) {
          padding-right: 0;
          padding-bottom: 15px; } }
    .menuContainer .menuList > div {
      max-width: 140px; }
    .menuContainer .menuList > div:not([data-list]) {
      margin-bottom: 5px; }
    .menuContainer .menuList > div[data-list] {
      display: table-cell; }
      .menuContainer .menuList > div[data-list]:not(:last-of-type) {
        padding-right: 15px; }
      .menuContainer .menuList > div[data-list] h6 {
        color: #ffc206;
        font-size: .8rem;
        margin: 0 0 5px 0; }
      .menuContainer .menuList > div[data-list] p {
        margin: 0; }

footer {
  padding: 20px;
  font-size: 0.8rem;
  color: gray;
  border-top: 1px solid rgba(128, 128, 128, 0.2);
  border-top-left-radius: 1px;
  border-top-right-radius: 1px; }
  footer p {
    margin: 10px 0 0; }
